import { InputHTMLAttributes, ReactNode } from 'react';
import { Slot } from '@radix-ui/react-slot';

export interface TextInputRootProps {
  children: ReactNode;
}

function TextInputRoot({ children }: TextInputRootProps) {
  return (
    <div 
      className="flex h-12 items-center bg-gray-800 gap-3 px-4 py-3 text-xs rounded w-full focus-within:ring-2 ring-cyan-300"
    >
      {children}
    </div>
  );
};

TextInputRoot.displayName = "TextInput.Root";

export interface TextInputInputProps extends InputHTMLAttributes<HTMLInputElement> {}

function TextInputInput(props: TextInputInputProps) {
  return (
      <input
        className="flex-1 bg-transparent outline-none text-gray-100 placeholder:text-gray-400"
        {...props}
      />
  );
};

TextInputInput.displayName = "TextInput.Input";

export interface TextInputIconProps {
  children: ReactNode;
}

function TextInputIcon({children}: TextInputIconProps) {
  return (
    <Slot className="w-6 h-6 text-gray-400">
      {children } 
    </Slot>
  );
};

TextInputIcon.displayName = "TextInput.Icon";

export const TextInput = {
  Root: TextInputRoot,
  Input: TextInputInput,
  Icon: TextInputIcon,
};
